/*-------------------------------------
 * Contact style
 -------------------------------------*/
.msg {
	opacity: 0;
	transition: opacity .3s;
	margin-top: 10px;
	margin-bottom: -20px;

	&.show {
		opacity: 1;
	}
}

.newsletter-block {

	input[type="text"] {
		width: 37%;
		border-color: #d7d7d7;
		display: inline-block;
		margin-right: 5px;
	}

	@media screen and (max-width: $screen-phone) {
		input[type="text"] {
			width: 70%;
			display: block;
			margin: 0 auto 20px;
		}
	}
}

.contact-item {
	display: inline-block;
	width: 300px;
	text-align: center;

	@media screen and (max-width: $screen-tablet) {
		margin-bottom: 40px;
	}

	.icon-wrapper {
		display: inline-block;
		margin-bottom: 24px;
	}

	.icon-box {
		background: #111111;
		color: #ffffff;
		display: table-cell;
		text-align: center;
		vertical-align: middle;
		border: solid 1px #000;
		min-width: 50px;
		min-height: 50px;
		width: 50px;
		height: 50px;
		transform: rotate(45deg);

		i {
			transform: rotate(-45deg);
		}
	}

	.title {
		@include font-size(1.2);
		letter-spacing: .1em;
		text-transform: uppercase;
		font-weight: 400;
	}
}

.map-block {
	position: absolute!important;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 0;

	a {
		padding: 36px 0;
		@include font-size(1.4);
		letter-spacing: .3em;
		color: #ffffff;
		display: block;
		transition: color .3s;
		z-index: 1;
		position: relative;

		@media screen and (max-width: $screen-tablet) {
			padding: 24px 0;
		}

		&:hover,
		&:focus,
		&:active {
			text-decoration: none;
			color: #ffffff;
		}

		&.close-map {
			display: none;
			font-weight: 400;
		}
	}
}

.map-content {
	position: absolute;
	top: 94px;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: #fff;
	transition: all .3s cubic-bezier(.42, 0, 1, 1);

	@media screen and (max-width: $screen-tablet) {
		top: 66px;
	}
}

#map-canvas {
	border: 0;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 1px;
	z-index: -2;
	width: 100%;
}

.map-section {
	overflow: hidden;

	&.style-2 {

		#map-canvas {
			z-index: 1;
		}

		.map-block {
			left: 15px;

			&:before {
				display: none;
			}

			@media (max-width: $screen-tablet) {
				position: relative!important;
				height: 300px;
				margin-top: -3.455em;
				left: 0;
			}
		}

		.container {
			position: static;
		}
	}

	.map-block {

		&:before {
			transition: all .3s cubic-bezier(.42, 0, 1, 1);
			content: "";
			display: block;
			background: #191919;
			opacity: .9;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			// z-index: -1;
		}
	}

	.container {
		position: relative;
		top: 0;
		transition: all .3s cubic-bezier(.42, 0, 1, 1);
		opacity: 1;
	}

	&.open {

		.map-block {

			a {
				color: #000000;

				&.close-map {
					display: block;
				}

				&.open-map {
					display: none;
				}
			}

			&:before {
				bottom: 100%;
			}
		}

		.map-content {
			top: 100%;
		}

		.container {
			opacity: 0;
		}
	}
}
