/*-------------------------------------
 * Blog style
 -------------------------------------*/
.post {

	margin-bottom: 4em;
	
	.entry-title {
		text-transform: uppercase;
		font-weight: 400;
		margin-top: 0;

		a {
			color: $listTitleColor;

			&:hover,
			&:focus {
				text-decoration: none;
			}
		}
	}

	.meta-data {
		text-transform: uppercase;
		@include font-size(1.3);
		letter-spacing: 0.2em;
		margin-bottom: 2em;

		a {

			&:hover,
			&:focus {
				text-decoration: none;
			}
		}
	}

	.entry-summary {
		@include font-size(1.6);

		.btn {

		}
	}

	.thumbnail {
		padding: 0;
		border: none;
		border-radius: 0;
	}
}

.vimeo {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	max-width: 100%;

	iframe,
	object,
	embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

/*-------------------------------------
 * Pagination
 -------------------------------------*/
.pagination > li > a,
.pagination > li > span {
	width: 38px;
	height: 38px;
	text-align: center;
	color: $linkColor;
	background-color: transparent;
	border: 1px solid $borderColor;
	margin: 0 1px;

	@media screen and (max-width: $screen-phone) {
		width: 20px;
		height: 20px;
		padding: 0;
	}
}

.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
	border-radius: 0;
}

.pagination > .active > a, 
.pagination > .active > a:hover,
.pagination > .active > a:focus,
.pagination > .active > span,
.pagination > .active > span:hover,
.pagination > .active > span:focus {
	background: transparent;
	color: $linkHoverColor;
	border-color: $HoverborderColor;
}

/*-------------------------------------
 * Pager
 -------------------------------------*/
.pager li > a,
.pager li > span {
	border-radius: 0;
	background: transparent;
}

/*-------------------------------------
 * Comments list
 -------------------------------------*/
#comments,
#respond {
	margin: 40px 0;
}
.comments-list {

	li.comment {
		overflow: hidden;
		margin-bottom: 1.5em;
	}

	ul.comment {
		margin-top: 4.5em;
	}
	
	.avatar {
		border-radius: 50%;
		width: 70px;
		height: auto;
		padding: 0;
	}

	.comment-body {
		width: calc(100% - 80px);

		.comment-header {
			text-transform: uppercase;
			letter-spacing: .23em;

			a {

				&:hover {
					text-decoration: none;
				}
			}

			> h4 {
				display: inline-block;
				margin: 0 12px 0 0;
			}
		}

		.comment-text {

			p {
				@include font-size(1.4);
			}
		}
	}
}

/*-------------------------------------
 * Latest Post style
 -------------------------------------*/
.latest-post {
	margin-bottom: 30px;

	@media screen and (max-width: $screen-tablet) {
		max-width: 370px;
		margin: 0 auto 30px;
	}

	.post-meta {
		@include font-size(1.2);
		letter-spacing: .1em;
		font-weight: 400;
		color: #999999;
		text-transform: uppercase;
	}

	p {
		color: #777777;
	}
}

/*-------------------------------------
 * Breadcrumb
 -------------------------------------*/
.breadcrumb {
	&.white {

		a,
		li {
			color: #fff;
		}
	}

	&.no-bg {
		background: transparent;
	}

	> li + li {

		&:before {
			content: "/";
			color: #777777;
		}
	}

	li {
		text-transform: uppercase;
		@include font-size(1.2);
		font-weight: 500;
		letter-spacing: .3em;
	}

	a {
		text-decoration: none;
	}
}