/*
 * Price slider 
 */
.price-title {
    text-align: right;
    color: #fff;
    text-transform: uppercase;
    @include font-size(1.3);
    letter-spacing: .2em;
    height: 40px;
    line-height: 40px;
    padding: 0;

    @media (max-width: $screen-tablet) {
        text-align: center;
    }

    &.dark {
        color: #222;
    }
}

.creative {
    padding-top: 19px;

    .ui-slider-horizontal {
        width: 100%;
        height: 4px;
        background: #f1f1f3;
        background: rgba(#f1f1f3, .3);
        border-radius: 4px;
        position: relative;
    }

    .ui-slider-range {
        background: #f1f1f3;
        background: rgba(#f1f1f3, .6);
        height: 4px;
        border-radius: 4px;
        position: absolute;
    }

    .ui-slider-handle {
        width: 12px;
        height: 12px;
        background: #f1f1f3;
        border-radius: 50%;
        display: block;
        position: absolute;
        top: -4px;
        margin-left: -6px;
        outline: 0;
        cursor: pointer;

        span {
            display: block;
            position: absolute;
            width: 100px;
            height: 20px;
            color: #fff;
            top: -25px;
            left: -43px;
            text-align: center;
            @include font-size(1.3);
            letter-spacing: .2em;
        }
    }

    &.dark {

        .ui-slider-horizontal {
            background: #222;
            background: rgba(#222, .3);
        }

        .ui-slider-range {
            background: #222;
            background: rgba(#222, .6);
        }

        .ui-slider-handle {
            background: #222;

            span {
                color: #222;
            }
        }
    }
}

/*
 * Sorting
 */
.select-sorting {
    padding-left: 30px;
    position: relative;

    .active {
        text-transform: uppercase;
        @include font-size(1.3);
        letter-spacing: .1em;
        height: 40px;
        line-height: 40px;
        color: #fff;
        cursor: pointer;

        i {
            margin-left: 10px;
        }
    }
    
    .list {
        list-style-type: none;
        padding: 0;
        margin: 0;
        border: none;
        position: absolute;
        padding: 10px 15px;
        left: 26px;
        background: #fff;
        background: rgba(#fff, .9);
        bottom: 0;
        border-radius: 1px;
        opacity: 0;
        visibility: hidden;
        transition: all .4s;

        @media (max-width: $screen-tablet) {
            text-align: left;
            left: 80%;
            margin-left: -50%;
        }

        li {

            text-transform: uppercase;
            @include font-size(1.3);
            letter-spacing: .1em;
            color: #222;
            cursor: pointer;
            transition: opacity .3s;

            &:hover {
                opacity: .8;
            }
        }
    }

    &.open {

        .list {
            visibility: visible;
            opacity: 1;
            left: 16px;

            @media (max-width: $screen-tablet) {
                left: 75%;
            }
        }
    }

    &.dark {
        padding-left: 15px;

        .active {
            color: #222;
        }
    }
}

/*
 * Shop widgets
 */
.widget {
    position: relative;
    z-index: 1;
}

/*
 * Cart
 */
.widget_shopping_cart {

    img {
        float: right;
        margin-left: 4px;
        width: 32px;
        height: auto;
        box-shadow: none;
    }

    .cart_list {
        list-style: none;
        padding: 0;
        margin: 20px 0 10px;

        li {
            padding-left: 2em;
            position: relative;
            padding-top: 0;

            .remove {
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }

    .quantity {
        display: block;
    }

    .total {
        @include font-size(1.4);
        text-transform: uppercase;
        letter-spacing: .1em;

        strong {
            font-weight: normal;
        }
    }
}

/*
 * Categories
 */
.widget_product_categories {
    
    ul {

        li {
            @include font-size(1.4);
            letter-spacing: .06em;

            .count {
                @include font-size(1.1);
                opacity: .8;
            }
        }
    }
}

/*
 * Products
 */
.widget_top_rated_products {

    .left {
        width: 35%;
    }

    .right {
        width: 60%;
    }

    img {
        max-width: 100%;
        margin-right: 10px;
    }

    .product-title {
        vertical-align: top;
        @include font-size(1.4);
        letter-spacing: .06em;
    }

    .star-rating {
        margin-top: 5px;
    }

    .amount {
        margin-top: 5px;
        display: block;
    }

    ul {

        li {
            padding: 10px 0;

            &:first-child {
                padding-top: 15px;
            }
        }
    }
}