.et-examples {

	> span {
		display: block;
	    width: 25%;
	    float: left;
	    padding: 0;
	    border: 1px solid #e5e5e5;
	    margin: -1px 0 0 -1px;
	    @include font-size(1.3);

	    @media (max-width: $screen-desktop) {
			width: 33.333%
	    }

	    @media (max-width: $screen-tablet) {
			width: 50%
	    }

	    @media (max-width: $screen-phone) {
			width: 100%
	    }

	    i {
			display: inline-block;
			margin-right: 5px;
			min-width: 70px;
			min-height: 70px;
			border-right: 1px solid #f1f1f1;
			line-height: 70px;
			text-align: center;
			@include font-size(3);
	    }

	    span {
			position: relative;
			top: -8px;
	    }
	}
}

