.woocommerce-review-link {
	line-height: 1;
	margin-left: 10px;
	display: block;
}

.shop-info {
	position: relative;
	margin-top: 20px;
	margin-bottom: 60px;
	
	p {
		@include font-size(1.4);
		margin-bottom: 0;
	}

	> i, span {
		@include font-size(5);
		position: absolute;
		opacity: .06;
		font-size: 50px;
		left: 20px;
		top: -25px;

	}
}

.single-product {

	.entry-summary {

		@media(max-width: $screen-desktop) {
			margin-bottom: 40px;
		}
	}

	.review-count {
		margin-left: 10px;
		@include font-size(1.3);
	}

	.comments-list {

		.star-rating {
			margin: 5px 0 9px;
		}
	}

	.thumblist {
		margin-top: 30px;

		.owl-prev,
		.owl-next {
			width: 25px;
			height: 40px;
			margin-top: -20px;

			i {
				top: 6px;
			}

			@media(max-width: $screen-desktop) {
				width: 20px;
				height: 20px;
				margin-top: -10px;

				i {
					top: 2px;
				}
			}
		}

		.owl-next {

			i {
				right: 6px;
			}
		}

		.owl-prev {

			i {
				left: 6px;
			}
		}
	}

	.onsale {
		right: 30px;
	}

	h1 {
		margin-bottom: 20px;
	}

	.price {
		@include font-size(3);
		margin-top: 15px;
		margin-bottom: 15px;

		del {
			@include font-size(1.7);
		}

		ins {
			text-decoration: none;
		}
	}

	.description {

		p {
			@include font-size(1.5);
			margin-bottom: 30px;
		}
	}

	.quantity {
		max-width: 80px;
		margin-right: 30px;

		input {
			width: 100%;
			padding: 10px 0 10px 10px;
			text-align: center;
			height: 40px;
		}
	}

	.single_add_to_cart_button {

		span {
			margin-right: 6px;
		}
	}
}