.team-item {
	position: relative;

	&:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: rgba($teamOverlayColor, .05);
		z-index: 1;
		transition: background .3s;
	}

	.description {
		position: absolute;
		top: 60%;
		left: 0;
		right: 0;
		text-align: right;
		padding: 0 15px;
		color: $teamDescriptionColor;
		z-index: 2;
		opacity: 0;
		transition: opacity .4s 0s;

		.team-item-name {
			@include font-size(1.6);
			position: relative;
			top: -15px;
			transition: top .4s 0s;
		}

		.team-item-role {
			@include font-size(1.1);
			letter-spacing: .1em;
			text-transform: uppercase;
			position: relative;
			opacity: .6;
			margin-top: 6px;
			bottom: -15px;
			transition: bottom .4s 0s;
		}
	}

	&:hover {
		&:before {
			background: rgba($teamOverlayColor, .8);
		}

		.description {
			opacity: 1;
			transition: opacity .4s .2s;

			.team-item-name {
				top: 0;
				transition: top .4s .2s;
			}

			.team-item-role {
				bottom: 0;
				transition: bottom .4s .2s;
			}
		}
	}
}