/*-------------------------------------
 * Products style
 -------------------------------------*/
.onsale {
	border: solid 1px #ff0054;
	color: #ff0054;
	position: absolute;
	top: 15px;
	right: 15px;
	line-height: 1;
	padding: 4px 7px;
	text-transform: uppercase;
	letter-spacing: .1em;
	@include font-size(1.3);
}
.products {
	padding: 0;
	margin: 0;
	list-style: none;

	.product {
		position: relative;

		> a {
			display: block;
			width: 100%;
		}

		a {
			text-decoration: none;
			opacity: 1;
			color: #000;
		}

		img {
			width: 100%;
		}
	}

	&.style-1 {
		
		.product {
			padding: 0;

			.price {
				position: absolute;
				left: 20px;
				top: 75px;
				text-transform: uppercase;
				@include font-size(1.3);
				background: #fff;
				background: rgba(#fff, .95);
				padding: 0 5px;
				opacity: 0;
				transition: all .4s;

				ins {
					text-decoration: none;
					margin-left: 10px;
				}
			}

			h3 {
				position: absolute;
				left: 30px;
				top: 100px;
				text-transform: uppercase;
				font-weight: 400;
				@include font-size(1.4);
				margin: 0;
				background: #fff;
				background: rgba(#fff, .85);
				padding: 0 5px;
				opacity: 0;
				transition: all .4s;
			}

			.add_to_cart_button {
				position: absolute;
				right: 15px;
				bottom: 45px;
				font-weight: 400;
				text-transform: uppercase;
				@include font-size(1.3);
				background: #fff;
				background: rgba(#fff, .95);
				padding: 0 6px;
				width: auto;
				letter-spacing: .1em;
				opacity: 0;
				transition: all .4s;

				span {
					margin-right: 6px;
					@include font-size(1.4);
				}
			}

			&:hover {

				.price {
					left: 30px;
					opacity: 1;
				}

				h3 {
					left: 15px;
					opacity: 1;
				}

				.add_to_cart_button {
					bottom: 65px;
					opacity: 1;
				}
			}
		}
	}

	&.style-2 {

		.product {
			margin-bottom: 30px;

			h3 {
				text-align: center;
				text-transform: uppercase;
				@include font-size(1.3);
				letter-spacing: .2em;
				margin-top: 15px;
				margin-bottom: 5px;
			}

			.onsale {
				right: 30px;
			}

			.price {
				text-align: center;
				text-transform: uppercase;
				@include font-size(1.3);
				letter-spacing: .1em;
				display: block;
				width: 100%;

				ins {
					text-decoration: none;
					margin-left: 10px;
				}
			}

			.add_to_cart_button {
				text-align: center;
				text-transform: uppercase;
				font-weight: 400;
				@include font-size(1.3);
				letter-spacing: .1em;
				margin-top: 5px;

				span {
					margin-right: 6px;
					@include font-size(1.4);
				}
			}
		}
	}
}

.star-rating {
    overflow: hidden;
    position: relative;
    height: 1em;
    line-height: 1em;
    font-size: 1em;
    width: 4.7em;
    font-family: "fontawesome";

    &:before {
		content: "\f006\f006\f006\f006\f006";
		float: left;
		top: 0;
		left: 0;
		position: absolute;
    }

    span {
		overflow: hidden;
		float: left;
		top: 0;
		left: 0;
		position: absolute;
		padding-top: 1.5em;

		&:before {
			content: "\f005\f005\f005\f005\f005";
			top: 0;
			position: absolute;
			left: 0;
		}
	}
}

.woocommerce-result-count {
	padding-right: 30px;
	height: 40px;
	line-height: 40px;
	text-transform: uppercase;
	letter-spacing: .2em;
	@include font-size(1.3);
	margin-bottom: 0;
	color: #fff;

	@media (max-width: $screen-tablet) {
		display: block;
		padding: 0;
		text-align: center;
		width: 100%;
	}

	&.dark {
		color: #222;
		padding-right: 0;
		margin-top: 16px;
	}
}

.top-nav {
	margin-top: -50px;
	margin-bottom: 10px;

	@media (max-width: $screen-tablet) {
		margin: 0;
		text-align: center;
		background: #222;
	}

	&.style-2 {
		margin: 36px 0;

		@media (max-width: $screen-tablet) {
			background: transparent;
		}
	}
}

.left-sidebar,
.right-sidebar {
	position: relative;
}

.left-sidebar {
	padding-right: 30px;

	.sidebar-bg {
		position: absolute;
		width: 9999px;
		height: 9999px;
		background-color: #f9f9f9;
		right: 0;
		top: 0;
	}
}

.right-sidebar {
	padding-left: 30px;

	.sidebar-bg {
		position: absolute;
		width: 9999px;
		height: 9999px;
		background-color: #f9f9f9;
		left: 0;
		top: 0;
	}
}