#WorldMap {
	position: relative;
	display: inline-block;
	max-width: 100%;
}
.worldmap-table {
	background: transparent;
	border-collapse: separate;
	@media ( max-width: 380px ) {
		transform: scale(.8);
		position: relative;
		left: -5%;
	}

	@media ( max-width: 340px ) {
		transform: scale(.8);
		position: relative;
		left: -10%;
	}
}
.worldmap-table tr {}
.worldmap-table th ,
.worldmap-table td {
	padding: 1px;
}
.worldmap-table th {}
.worldmap-table td {}
.worldmap-table .dot {
	width: 2px;
	height: 2px;

	@media ( max-width: $screen-tablet ) {
		width: 1px;
		height: 1px;		
	}
}
.worldmap-table .dot.ground {
	background: black;
	border-radius: 50%;
}

.dot-animation {
	animation: zoomDot 4s;
}

.dot-animation-small {
	animation: zoomDotSmall 3s;
}

.plane {
	background-size: contain;
	transform: scale(0.7);
}

.plane-top {
	background: transparent url(../img/plane-top.png) no-repeat 50% 50%;
	animation: planeTop 5s infinite;
	position: absolute;
	width: 25px;
	height: 90px;
}

.plane-right {
	background: transparent url(../img/plane-right.png) no-repeat 50% 50%;
	animation: planeRight 5s infinite;
	position: absolute;
	width: 90px;
	height: 25px;
}

.plane-bottom {
	background: transparent url(../img/plane-bottom.png) no-repeat 50% 50%;
	animation: planeBottom 5s infinite;
	position: absolute;
	width: 25px;
	height: 90px;
}

.plane-left {
	background: transparent url(../img/plane-left.png) no-repeat 50% 50%;
	animation: planeLeft 5s infinite;
	position: absolute;
	width: 90px;
	height: 25px;
}

@keyframes zoomDot {
	0% {
		background: black!important;
		transform: scale(1);
	}
	1% {
		background: #ff0024!important;
	}
	50% {
		background: #ff0024!important;
		transform: scale(2);
	}
	100% {
		background: #ff0024!important;
		transform: scale(1);
	}
}

@keyframes zoomDotSmall {
	0% {
		background: black!important;
		transform: scale(1);
	}
	1% {
		background: #ff0024!important;
	}
	50% {
		background: #ff0024!important;
		transform: scale(1.5);
	}
	100% {
		background: #ff0024!important;
		transform: scale(1);
	}
}

@keyframes planeTop {
	0% {
		top: calc(100% - 50px);
		opacity: 0;
	}
	25% {
		opacity: 1;
	}
	750% {
		opacity: 1;
	}
	100% {
		top: 0%;
		opacity: 0;
	}
}

@keyframes planeRight {
	0% {
		left: 0%;
		opacity: 0;
	}
	25% {
		opacity: 1;
	}
	750% {
		opacity: 1;
	}
	100% {
		left: calc(100% - 50px);
		opacity: 0;
	}
}

@keyframes planeBottom {
	0% {
		top: 0%;
		opacity: 0;
	}
	25% {
		opacity: 1;
	}
	750% {
		opacity: 1;
	}
	100% {
		top: calc(100% - 50px);
		opacity: 0;
	}
}

@keyframes planeLeft {
	0% {
		left: calc(100% - 50px);
		opacity: 0;
	}
	25% {
		opacity: 1;
	}
	750% {
		opacity: 1;
	}
	100% {
		left: 0%;
		opacity: 0;
	}
}