/*-------------------------------------
 * Checkout
 -------------------------------------*/
 .woocommerce {
 	.checkout_right_wrapper {
 		@media(max-width: 991px) {
 			padding: 0 30px;
 		}
 	}
 	.login {
 		overflow: hidden;
 		.form {
			border: 1px solid #e9e9e9;
			padding: 15px;
 		}
 		.lost_password {
 			float: right;
 			text-align: right;
			display: inline;
			// margin-left: 20px;
			vertical-align: middle;
 		}
 	}
 	.checkout_coupon {
 		font-size: 0;
 		input {
 			display: inline-block;
 			width: auto;
 			margin: 2rem 0;
 		}
 		@media(max-width: 480px) {
 			.form-control {
 				height: 24px;
 			}
 		}
 	}
 	.woocommerce-billing-fields {
 		text-align: left;
 		.form-row  {
 			display: inline-block;
 			margin-bottom: 2rem;
 			input {
 				float: left;
 				margin: 0;
 			}
 		}
 		.create-account {
 			p {
 				text-transform: none;
 			}
 		}
 	}
 	.woocommerce-shipping-fields {
 		text-transform: uppercase;
 		text-align: left;
 		.form-row  {
 			display: inline-block;
 			margin-bottom: 2rem;
 			input {
 				float: left;
 				margin: 0;
 			}
 		}
 	}
 	.order_review_wrapper {
 		text-transform: uppercase;
		overflow: hidden;
		border: 1px solid #e9e9e9;
		p {
			text-transform: none;
		}
		.shop_table {
			th {
				width: 50%;
				padding: 0 20px;
				font-weight: 400;
			}
			tr {
				height: 5rem;
				&.shipping {
					vertical-align: top;
				}
			}
			strong {
						font-weight: 400;
					}
			thead {
				th:first-child {
					border-right: 1px solid;
					border-color: #e9e9e9;
				}
				th:last-child {
					border-left: 1px solid;
					border-color: #e9e9e9;
				}
			}
			tbody {
				td {
					border: 1px solid;
					border-bottom: none;
					border-color: #e9e9e9;
					padding: 0 20px;
					&.product-total {
						text-align: center;
					}
				}
				td:first-child {
					border-left: none;
				}
				td:last-child {
					border-right: none;
				}
			}
			tfoot {
				td {
					text-align: left;
				}

			}
		}
		.woocommerce-checkout-payment {
			.payment_methods {
				list-style: none;
				text-align: left;
				.payment_method_paypal {
					img {
						height: 60px;
						margin-top: -36px;
						top: 12px;
						position: relative;
						padding: 0 10px;
					}
				}
			}
			.btn {
				width: 100%;
				&#place_order {
					margin: 20px 0 30px 0;
				}
			}
		}
	}
}