/*-------------------------------------
 * Cart
 -------------------------------------*/

.woocommerce {
	abbr {
		border: none;
	}
	a {
		cursor: pointer;
	}
}
.cart-page {
	width: 100%;
	text-align: center;
	.cart {
		width: 100%;
		a {
			text-decoration: none;
			font-size: 13px;
			letter-spacing: 0.085em;
			font-weight: 400;
			color: #302f2f;

			&.remove {
				color: #6a6a6a;
				font-size: 18px;
				font-weight: 500;
			}
		}
	}
	.shop_table {
		thead {
			tr {
				th {
					font-size: 13px;
					color: #302f2f;
					text-transform: uppercase;
					letter-spacing: 0.085em;
					font-weight: 600;
					text-align: center;
					height: 37px;
					padding: 0 20px;
					border-left: 1px solid;
					border-right: 1px solid;
					border-color: #e9e9e9;
					@media(max-width: 768px) {
						display: none;
					}
					&:first-child {
						border-left: none;
					}
					&:last-child {
						border-right: none;
					}
				}
				@media(min-width: 768px) {
					.product-remove {
						width: 10.3%;
					}
					.product-subtotal {
						width: 12.4%;
					}
					.product-quantity {
						width: 21.5%;
					}
					.product-price {
						width: 11.9%;
					}
					.product-thumbnail {
						width: 15%;
					}
					.product-name {
						text-align: left;
						padding-left: 20px;
					}
				}
			}
		}

		.cart_item {

			@media(max-width: 768px) {
				border-bottom: 1px solid #e9e9e9;
				display: block;
			}

			td {
				border: 1px solid #e9e9e9;
				padding: 0 20px;

				@media(max-width: 768px) {
					display: block;
					border: none;
					margin-top: 10px;
				}

				input {
					width: 60px;
					padding-left: 5px;
					border: 1px solid #e9e9e9;
					text-align: center;
				}

				.amount {
					font-size: 13px;
					letter-spacing: 0.085em;
				}
			}

			> :first-child {
				border-left: none;

				@media(max-width: 768px) {
					margin-bottom: 20px;
				}
			}

			> :last-child {
				border-right: none;

				@media(max-width: 768px) {
					margin-bottom: 10px;
				}
			}

			.product-thumbnail {
				margin: 20px auto 0;
				padding: 0;

				@media(max-width: 768px) {
					width: 35%;
				}
			}

			.product-name {
				text-align: left;
				font-weight: 400;
				text-transform: uppercase;

				@media(max-width: 768px) {
					text-align: center;
				}
			}
		}

		.coupon {
			margin-top: 20px;
			font-size: 0;

			@media(max-width: 991px) {
				overflow: hidden;
				text-align: right;

			}

			.form-control {
				width: auto;
				display: inline;
				@media(max-width: 768px) {
					padding: 0 1.363em;
				}
				@media(max-width: 480px) {
					height: 29px;
					width: 50%;
				}
			}

			input {
				float: left;
				margin: 0;
				@media(max-width: 991px) {
					float: none;
				}
			}
		}
		.checkout {

			input, button {
				float: right;
				margin-right: 0;
				margin-left: 4px;
				overflow: hidden;

				@media(max-width: 768px) {
					margin-left: 4px;
				}
			}

			i {
				font-size: 13px;
				position: relative;
				float: right;
				margin-left: -70px;
				padding-right: 8px;
				padding-top: 6px;
				height: 29px;
				display: none;

				&:before {
					vertical-align: middle;
				}
			}

			@media(max-width: 991px) {
				overflow: hidden;
				margin-top: 10px;
			}

			@media(max-width: 768px) {
				text-align: left;
				font-size: 0;
			}

			@media(max-width: 480px) {
				input {
					font-size: 0;
					width: 29px;
					height: 29px;
					z-index: 2;
					position: relative;
					border: none;
				}

				i {
					display: inline-block;
				}
			}
		}
	}
}

.cart-collaterals {
	overflow: hidden;
}

.cart_totals {
	float: right;
	text-transform: uppercase;
	letter-spacing: .1em;
	text-align: left;

	td {
		padding-left: 20px;
	}

	th {
		font-weight: 400;
	}

	.shipping {

		th {
			vertical-align: top;
		}
	}

	.amount {
		font-weight: 400;
	}
}

.order-total {
	@include font-size(2.4);
}

