/*-------------------------------------
 * Page header
 -------------------------------------*/
.section-header {
	border-bottom: none;
	margin: 0;
	padding: 5.5em 0 4em;
	position: relative;
	background-size: cover;
	z-index: 0;

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin: 0;
		text-align: center;
		@include font-size($pageHeaderFontSize);
		color: $pageHeaderColor;
		letter-spacing: $pageHeaderLetterSpacing;

		&:after {
			content: "";
			display: block;
			width: 3.875em;
			height: 1px;
			background: rgba($pageHeaderLineColor, .2);
			margin: 1.500em auto 0;
			position: relative;
			left: -3px;
		}
	}

	.sub-title {
		color: #908c8c;
		@include font-size(1.4);
	}
}

.sub-header {
	letter-spacing: .2em;
	@include font-size(1.4);
}

/*-------------------------------------
 * Footer
 -------------------------------------*/
footer.site-footer {
	background: $footerBgColor;
	padding: $footerTopOffset 0 $footerBottomOffset;

	.copyright {
		color: $copyrightColor;
		@include font-size($copyrightFontSize);

		.company {
			letter-spacing: 0.2em;
		}
	}

	& > div + div {
		margin-top: 4.700em;
	}
}

.mfp-iframe-holder .mfp-content {
	vertical-align: middle;
}

/*-------------------------------------
 * Layer animation
 -------------------------------------*/
.layers img {
	position: relative;
	z-index: 1;
}

.layers .layer {
	display: block;
	position: absolute;
	z-index: 1;
	max-width: 90%;
}

[data-layer-animation] {
	visibility: hidden;
}

.animated[data-layer-animation] {
	visibility: visible;
	z-index: 0;
}

/*-------------------------------------
 * Promo page
 -------------------------------------*/
.demo-list {
	margin-bottom: -70px;
	.demo-item {
		margin-bottom: 70px;
		transition: all .3s;

		&:hover {
			opacity: .8;
		}

		a {

			&:hover {
				text-decoration: none;
			}
		}

		img {
			display: block;
			margin: auto;
		}
	}
}