.skill {
	clear: both;
	overflow: hidden;
	position: relative;
	margin-bottom: 6px;

	@media screen and (min-width: $screen-tablet) and (max-width: $screen-lg-desktop) {
		overflow: visible;
	}

	@media screen and (max-width: $screen-phone) {
		overflow: visible;
	}

	.title {
		@include font-size(1.3);
		color: rgba($progressBarColor, .4);
		float: left;
		width: 150px;

		@media screen and (min-width: $screen-tablet) and (max-width: $screen-lg-desktop) {
			width: 100%;
			float: none;
		}

		@media screen and (max-width: $screen-tablet) {
			width: 130px;
		}

		@media screen and (max-width: $screen-phone) {
			width: 100%;
			float: none;
		}
	}

	.skill-progress-bar {
		position: absolute;
		top: 50%;
		left: 150px;
		float: left;
		height: 1px;
		background: rgba($progressBarColor, .25);
		width: 100%;
		max-width: 0;
		transition: max-width 1.5s cubic-bezier(0.215, 0.61, 0.355, 1);

		@media screen and (min-width: $screen-tablet) and (max-width: $screen-lg-desktop) {
			position: relative;
			top: auto;
			left: auto;
			float: none;
		}

		@media screen and (max-width: $screen-desktop) {
			&:before {
				top: -9px;
			}
		}

		@media screen and (max-width: $screen-tablet) {
			&:before {
				top: -7px;
			}
		}

		@media screen and (max-width: $screen-phone) {
			position: relative;
			top: auto;
			left: auto;
			float: none;

			&:before {
				top: -6px;
				margin-left: 5px;
			}
		}

		@for $i from 1 through 100 {
			&[data-progress-animation="#{$i}"].animated {
				max-width: calc(250px / 100 * #{$i});
			}
		}

		&:before {
			@include font-size(1.1);
			color: rgba($progressBarColor, .25);
			content: attr(data-progress-text);
			display: block;
			position: absolute;
			top: -10px;
			left: 100%;
			margin-left: 10px;
		}
	}

	&.dark {

		.title {
			color: rgba($progressBarDarkColor, .9);
		}

		.skill-progress-bar {
			background: rgba($progressBarDarkColor, .65);

			&:before {
				color: rgba($progressBarDarkColor, .65);
			}
		}
	}
}
