/*-------------------------------------
 * Page header
 -------------------------------------*/
.section-header {
  border-bottom: none;
  margin: 0;
  padding: 5.5em 0 4em;
  position: relative;
  background-size: cover;
  z-index: 0; }
  .section-header h1,
  .section-header h2,
  .section-header h3,
  .section-header h4,
  .section-header h5,
  .section-header h6 {
    margin: 0;
    text-align: center;
    font-size: 16px;
    font-size: 1.6rem;
    color: #ffffff;
    letter-spacing: 0.5em; }
    .section-header h1:after,
    .section-header h2:after,
    .section-header h3:after,
    .section-header h4:after,
    .section-header h5:after,
    .section-header h6:after {
      content: "";
      display: block;
      width: 3.875em;
      height: 1px;
      background: rgba(255, 255, 255, 0.2);
      margin: 1.500em auto 0;
      position: relative;
      left: -3px; }
  .section-header .sub-title {
    color: #908c8c;
    font-size: 14px;
    font-size: 1.4rem; }

.sub-header {
  letter-spacing: .2em;
  font-size: 14px;
  font-size: 1.4rem; }

