 * Layer animation
 -------------------------------------*/
.layers img {
  position: relative;
  z-index: 1; }

.layers .layer {
  display: block;
  position: absolute;
  z-index: 1;
  max-width: 90%; }

[data-layer-animation] {
  visibility: hidden; }

.animated[data-layer-animation] {
  visibility: visible;
  z-index: 0; }

